import React, { useEffect, useState, useRef } from "react"
import MetaTags from "react-meta-tags"
import PropTypes from "prop-types"
import { withRouter, Link } from "react-router-dom"
import { isEmpty } from "lodash"
import BootstrapTable from "react-bootstrap-table-next"
import cellEditFactory from "react-bootstrap-table2-editor"
import Dropzone from "react-dropzone"
import paginationFactory, {
  PaginationListStandalone,
  PaginationProvider,
} from "react-bootstrap-table2-paginator"
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit"
import * as moment from "moment"
import axios from "axios"
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  Form,
  Col,
  Container,
  Row,
  Badge,
  UncontrolledTooltip,
  Modal,
  ModalHeader,
  ModalBody,
} from "reactstrap"
import { AvForm, AvField } from "availity-reactstrap-validation"

//redux
import { useSelector, useDispatch } from "react-redux"

//Import Breadcrumb
import Breadcrumbs from "components/Common/Breadcrumb"
import { currency } from "../../../helpers/currency"
import toastr from "toastr"
import "toastr/build/toastr.min.css"

const EcommerceOthers = props => {
  const dispatch = useDispatch()

  const [stores, setStores] = useState([])
  const [modal, setModal] = useState(false)

  const { status } = useSelector(state => ({
    status: state.ecommerce.status,
  }))

  const [isdisabled, setisdisabled] = useState(false)

  useEffect(() => {
    getstores()
  }, [])
  //pagination customization
  const pageOptions = {
    sizePerPage: 15,
    totalSize: stores.length, // replace later with size(products),
    custom: true,
  }
  currency

  const { SearchBar } = Search

  // const toggleModal = () => {
  //   setModal1(!modal1)
  // }
  const toggleViewModal = () => setModal1(!modal1)

  const EcommerceStoresColumns = toggleModal => [
    {
      dataField: "id",
      text: " ID",
      sort: true,
      // eslint-disable-next-line react/display-name
      formatter: (cellContent, row, index) => (
        <Link to="#" className="text-body fw-bold">
          {index + 1}
        </Link>
      ),
    },
    {
      dataField: "image",
      text: "image",
      sort: true,
      // eslint-disable-next-line react/display-name
      formatter: (cellContent, row) => (
        <div
          data-toggle="tooltip"
          data-placement="top"
          className="cursor-pointer"
        >
          <img
            src={row.image}
            className="rounded"
            style={{ width: "30px", height: "30px" }}
          />
        </div>
      ),
    },
    {
      dataField: "name",
      text: "name",
      sort: true,
      // eslint-disable-next-line react/display-name
      formatter: (cellContent, row) => (
        <div
          data-toggle="tooltip"
          data-placement="top"
          className="cursor-pointer"
        >
          {row.name}
        </div>
      ),
    },
    {
      dataField: "location",
      text: "location",
      sort: true,
    },

    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      // eslint-disable-next-line react/display-name
      formatter: (cellContent, row) => (
        <>
          <div className="square-switch">
            <input
              type="checkbox"
              id={"square-switch" + row.id}
              switch="none"
              checked={row.status ? true : false}
              onChange={e => {
                handleCheckBox(row.id, !row.status)
              }}
            />
            <label
              className="mb-0"
              htmlFor={"square-switch" + row.id}
              data-on-label="On"
              data-off-label="Off"
            />
          </div>
        </>
      ),
      editable: () => {
        return false
      },
    },
  ]

  const EcommercestoresColumns = () => [
    {
      dataField: "store",
      text: "store",
      sort: true,
      // eslint-disable-next-line react/display-name
      formatter: (cellContent, row) => (
        <div
          data-toggle="tooltip"
          data-placement="top"
          title="Double click to edit"
          className="cursor-pointer"
          onClick={() => switchPrice(row)}
        >
          {row.store}
        </div>
      ),
    },
    {
      dataField: "action",
      isDummyField: true,
      text: "Action",
      // eslint-disable-next-line react/display-name
      formatter: (cellContent, store) => (
        <>
          <div className="d-flex gap-3">
            <Link
              to="#"
              className="text-danger"
              onClick={() => handlestoreDelete(store)}
            >
              <i className="mdi mdi-delete font-size-18" id="deletetooltip" />
              <UncontrolledTooltip placement="top" target="deletetooltip">
                Delete
              </UncontrolledTooltip>
            </Link>
          </div>
        </>
      ),
    },
  ]
  const handleCheckBox = (id, value) => {
    var data = { id, active: value }
    axios
      .put(`${process.env.REACT_APP_URL}/change/store-status/${id}`, data)
      .then(res => {
        if (res.status === 200) {
          getstores()
        }
      })
  }

  const toggle = () => {
    setModal(!modal)
  }
  const togglestore = () => {
    setstoreModal(!storeModal)
  }

  const toLowerCase1 = str => {
    return str.toLowerCase()
  }

  var node = useRef()
  const onPaginationPageChange = page => {
    if (
      node &&
      node.current &&
      node.current.props &&
      node.current.props.pagination &&
      node.current.props.pagination.options
    ) {
      node.current.props.pagination.options.onPageChange(page)
    }
  }
  function getstores(id) {
    axios.get(`${process.env.REACT_APP_URL}/get-stores`).then(res => {
      if (res.status === 200) {
        setStores(res.data.data)
      }
    })
  }

  const handleDelete = data => {
    let confirm = window.confirm("Are you sure?")
    if (confirm) {
      if (data.id !== undefined) {
        axios
          .delete(`${process.env.REACT_APP_URL}/stores/${data.id}`)
          .then(res => {
            if (res.status === 200) {
              toastr.success("Updated")

              let filteredstore = [...stores].filter(
                item => Number(item.id) !== Number(data.id)
              )

              setStores(filteredstore)
            }
          })
      }
    }
  }

  const handleTableUpdate = (id, value, column) => {
    var data = { id }

    switch (column) {
      case "standard_fee":
        data.standard_fee = value
        break
      case "express_fee":
        data.express_fee = value
        break
      case "scheduled_fee":
        data.scheduled_fee = value
        break

      default:
        break
    }
    let confirm = window.confirm("Are you sure?")
    if (confirm) {
      axios.put(`${process.env.REACT_APP_URL}/stores/${id}`, data).then(res => {
        if (res.status === 200) {
          toastr.success("Updated")
        }
      })
    }
  }

  const defaultSorted = [
    {
      dataField: "id",
      order: "desc",
    },
  ]

  return (
    <React.Fragment>
      <div className="page-content">
        <MetaTags>
          <title>Stores | EnterMarket</title>
        </MetaTags>
        <Container fluid>
          <Breadcrumbs title="stores" breadcrumbItem="Fees" />
          <div className="d-flex align-items-center text-sm text-info mb-2">
            {" "}
            <i className="fa fa-info-circle mr-1"></i>{" "}
            <p className="mb-0">Double click on column to update</p>
          </div>
          <Row className="justify-content-center">
            <Col xs="12">
              <Card>
                <CardBody>
                  <PaginationProvider
                    pagination={paginationFactory(pageOptions)}
                    keyField="id"
                    columns={EcommerceStoresColumns(toggle)}
                    data={stores}
                  >
                    {({ paginationProps, paginationTableProps }) => (
                      <ToolkitProvider
                        keyField="id"
                        data={stores}
                        columns={EcommerceStoresColumns(toggle)}
                        bootstrap4
                        search
                      >
                        {toolkitProps => (
                          <React.Fragment>
                            <Row className="mb-2">
                              <Col sm="4">
                                <div className="search-box me-2 mb-2 d-inline-block">
                                  <div className="position-relative">
                                    <SearchBar {...toolkitProps.searchProps} />
                                    <i className="bx bx-search-alt search-icon" />
                                  </div>
                                </div>
                              </Col>

                            </Row>
                            <Row>
                              <Col>
                                <div className="table-responsive">
                                  <BootstrapTable
                                    keyField="id"
                                    responsive
                                    bproducted={false}
                                    striped={false}
                                    defaultSorted={defaultSorted}
                                    cellEdit={cellEditFactory({
                                      mode: "dbclick",
                                      blurToSave: true,
                                      beforeSaveCell(
                                        oldValue,
                                        newValue,
                                        row,
                                        column,
                                        done
                                      ) {
                                        handleTableUpdate(
                                          row.id,
                                          newValue,
                                          column.dataField
                                        )
                                        done()
                                        return { async: true }
                                      },
                                    })}
                                    classes={
                                      "table align-middle table-nowrap table-check"
                                    }
                                    headerWrapperClasses={"table-light"}
                                    {...toolkitProps.baseProps}
                                    {...paginationTableProps}
                                    ref={node}
                                  />
                                </div>
                              </Col>
                            </Row>
                            <Row className="align-items-md-center mt-30">
                              <Col className="pagination pagination-rounded justify-content-end mb-2 inner-custom-pagination">
                                <PaginationListStandalone
                                  {...paginationProps}
                                />
                              </Col>
                            </Row>
                          </React.Fragment>
                        )}
                      </ToolkitProvider>
                    )}
                  </PaginationProvider>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    </React.Fragment>
  )
}

EcommerceOthers.propTypes = {
  stores: PropTypes.array,
}

export default withRouter(EcommerceOthers)
