import React from "react"
import { Redirect } from "react-router-dom"

// Profile

// //Ecommerce Pages

import EcommerceOthers from "../pages/Ecommerce/Others/categories"
import EcommerceLgas from "../pages/Ecommerce/Others/lgas"
import EcommerceStores from "../pages/Ecommerce/Others/stores"

// Authentication related pages

//Admin login
import AdminLogin from "../pages/Admin/Authentication/Login"
import AdminLogout from "../pages/Admin/Authentication/Logout"
import AdminRegister from "../pages/Admin/Authentication/Register"
import AdminForgetPwd from "../pages/Admin/Authentication/ForgetPassword"

//  // Inner Authentication

// Dashboard

import AdminDashboard from "../pages/Admin/Dashboard/index"

//Admin Orders
import AdminOrders from "../pages/Admin/Orders/index"
import AdminOrdersAssigned from "../pages/Admin/Orders/assigned"
import AdminOrdersPending from "../pages/Admin/Orders/pending"

const publicRoutes = [
  { path: "/logout", component: AdminLogout },
  { path: "/login", component: AdminLogin },
  { path: "/forgot-password", component: AdminForgetPwd },
  { path: "/register", component: AdminRegister },
]

const adminProtectedRoutes = [
  { path: "/", component: AdminDashboard },
  { path: "/orders", component: AdminOrders },
  { path: "/orders/assigned", component: AdminOrdersAssigned },
  { path: "/orders/pending", component: AdminOrdersPending },
  { path: "/others", component: EcommerceOthers },
  { path: "/fees", component: EcommerceLgas },
  { path: "/stores", component: EcommerceStores },

  {
    path: "/dashboard",
    exact: true,
    component: () => <Redirect to="/login" />,
  },
  {
    path: "*",
    exact: true,
    component: () => <Redirect to="/" />,
  },
]

export { adminProtectedRoutes, publicRoutes }
